













































































































































import Vue from 'vue'
import Header from '../components/Header.component.vue'
import Quill from 'quill'
import { format } from '../utils/format.util'
import { editorMixin } from '../mixins/editor.mixin'

export default Vue.extend({
  mixins: [editorMixin],
  data: function() {
    return {
      // https://picsum.photos/id/68/536/354
      title: '',
      cover: 'https://picsum.photos/id/68/536/354',
      editor: {} as Quill,
      articleHtml: '这里添加文章的内容...',
      previewMode: false
    }
  },
  components: {
    Header
  },
  methods: {
    onBack() {
      this.$router.push({
        name: 'home'
      })
    },
    saveAsDraft() {
      console.log('saveAdDraft')
    },
    publish() {
      console.log('publish')

      this.$validator.validate().then(valid => {
        if (valid) {
          console.log('TCL: publish -> valid', valid)
        }
      })
    }
  },
  mounted() {
    const that: any = this
    this.editor = new Quill(that.$refs.editor, {
      modules: {
        toolbar: false
      },
      theme: 'snow'
    })
  }
})
